<template>
	<view class="uni-common-mt">
		<form >
			<input class="hidden" v-model="formData.id"  v-if="edit=='1'"/>
			<view class="uni-form-item uni-row">
				<view class="title">申请公司</view>
				<input class="hidden" name="companyId" v-model="formData.companyId">
				<input class="hidden" name="deptId" v-model="formData.deptId">
				<picker  class="picker" mode="multiSelector" @columnchange="bindMultiPickerColumnChange" :value="multiIndex" :range="multiArray">
					<view class="uni-input">{{multiArray[0][multiIndex[0]]}}，{{multiArray[1][multiIndex[1]]}}，{{multiArray[2][multiIndex[2]]}}</view>
				</picker>
			</view>
			<view class="uni-form-item uni-row">
				<view class="title"><span class="red">*</span>上班时间</view>
				<input class="hidden" name="workTime"   v-model="formData.workTime"/>
				<e-picker class="uni-input" mode="dateTime" :showValue="(formData.workTime?formData.workTime:dateStart)"  @change="changeStart">{{(formData.workTime?formData.workTime:dateStart)}}</e-picker>
				<!-- <input class="uni-input" name="goOutStart" focus placeholder="外出起始时间" /> -->
			</view>
			<view class="uni-form-item uni-row">
				<view class="title"><span class="red">*</span>下班时间</view>
				<input class="hidden" name="offTime"   v-model="formData.offTime"/>
				<e-picker class="uni-input" mode="dateTime" :showValue="(formData.offTime?formData.offTime:dateEnd)"  @change="changeEnd">{{(formData.offTime?formData.offTime:dateEnd)}}</e-picker>
			</view>
			<view class="uni-form-item uni-row">
				<view class="title"><span class="red">*</span>打卡到公司距离</view>
				<input class="uni-input" name="distance"  placeholder="打卡到公司距离" v-model="formData.distance"/>
			</view>
			<view class="uni-form-item uni-row">
				<view class="title">经度</view>
				<input class="uni-input" name="longitude" v-model="formData.longitude"   placeholder="经度" @tap="getMap()"/>
			</view>
			<view class="uni-form-item uni-row">
				<view class="title">纬度</view>
				<input class="uni-input" name="latitude"  v-model="formData.latitude"  placeholder="纬度" />
			</view>
			<view class="uni-form-item uni-row">
				<view class="title">备注</view>
				<input class="uni-input" name="remark"  v-model="formData.remark"  placeholder="备注" />
			</view>
			<view class="button-sp-area">
				<button class="mini-btn" type="primary" @click="submitHandler('2')" :loading="loading1" v-if="formData.status=='3'"  size="mini">提交</button>
				<button class="mini-btn" type="primary" @click="submit('1')" :loading="loading2" v-if="formData.status=='99'||formData.status==''"  size="mini">提交</button>
			</view>
		</form>
	</view>
</template>

<script>
	import service from '@/service.js';
	import graceChecker from '@/common/graceChecker.js'
	import ePicker from '@/components/e-picker/e-picker.vue';
	export default {
		components: {
			ePicker
		},
		data() {
			return {
				id:'',
				edit:'',
				user:{},
				formData:{
					id:'',
					userName:'',
					companyId:'',
					deptId:'',
					workTime:service.getDate('dateTime'),
					offTime:service.getDate('dateTime'),
					distance:0,
					latitude:'',
					longitude:'',
					remark:'',
					status:''
				},
				baseUrl:'oa/oaAttendanceConfig',
				addUrl:'oa/oaAttendanceConfig/add',
				flag:false,
				index: 0,
				date: service.getDate('dateTime'),
				dateStart:service.getDate('dateTime'),
				dateEnd:service.getDate('dateTime'),
				timeLimit:'',
				company:[],
				multiArray: [
					['百川'],
					[],
					[]
				],
				multiArrayId: [
					['100'],
					[],
					[]
				],
				multiIndex: [0, 0, 0],
				loading1:false,
				loading2:false,
				loading3:false,
				procInstId:''
			}
		},
		onLoad(e) {
			this.edit=e.edit || ''
			this.id = e.id || ''
			let this_=this
			//用户信息
			this.urlRequestPost('/phoneIndexData','',null,function(res){
				if(res.statusCode='200'){
					this_.user=res.data.user
					this_.$set(this_.formData,'companyId',res.data.user.companyId)
					this_.$set(this_.formData,'deptId',res.data.user.deptId)
					// 公司集合
					this_.urlRequestGet('system/dept/list?parentId=100','text/html;charset=UTF-8',function(res){
						if(res.statusCode='200'){
							this_.company=res.data;
							let companyList=[]
							let companyIdList=[]
							let initId=0
							for (let i in this_.company) {
								if(this_.company[i].deptId==this_.user.companyId){
									initId=i
								}
								companyList.push(this_.company[i].deptName)
								companyIdList.push(this_.company[i].deptId)
							}
							this_.multiArray[1]=companyList
							this_.multiArrayId[1]=companyIdList
							//默认展示的是当前用户的公司
							this_.showDeptDetail(initId)
						}
					})
				}
			})
			//编辑页面信息
			if(this.edit=='1'){
				this.urlRequestGet(this.baseUrl+'/detail/'+this.id,'text/html;charset=UTF-8',function(res){
					console.log(res)
					service.listAssign(this_.formData,res.data)
				})
			}
		},
		onReady() {
			let this_=this
		},
		methods: {
			//地图
			getMap(){
				var this_=this
				uni.chooseLocation({
				    success: function (res) {
				        console.log('位置名称：' + res.name);
				        console.log('详细地址：' + res.address);
				        console.log('纬度：' + res.latitude);
				        console.log('经度：' + res.longitude);
						this_.formData.longitude=res.longitude
						this_.formData.latitude=res.latitude
				    }
				});
			},
			// 提交和保存
			submit(flag){
				let this_=this
				//定义表单规则			
				let rule = [
				    {name:"distance", checkType : "notnull", checkRule:"",  errorMsg:"打卡距离必填"},
				    {name:"latitude", checkType : "notnull", checkRule:"",  errorMsg:"纬度必填"},
				    {name:"longitude", checkType : "notnull", checkRule:"",  errorMsg:"经度必填"}
				];
				//进行表单检查
				let checkRes = graceChecker.check(this.formData, rule);
				if(checkRes){
					this.urlRequestPost(this.addUrl,this.formData,null,function(res){
						this_.loading1=false
						this_.loading2=false
						uni.showToast({title:res.data.msg, icon:"none"});
						if(res.data.code=='0'){
							uni.reLaunch({
							    url: '../clock/clock'
							});
						}
					})
				}else{
				    uni.showToast({ title: graceChecker.error, icon: "none" });
				}
			},
			changeStart: function(e) {
				this.formData.workTime=e
				this.showValue = e
				this.dateStart = e
				this.getTimeLimit(this.dateStart,this.dateEnd)
			},
			changeEnd: function(e) {
				this.formData.offTime=e
				this.showValue = e
				this.dateEnd = e
				this.getTimeLimit(this.dateStart,this.dateEnd)
			},
			bindMultiPickerColumnChange: function(e) {
				this.multiIndex[e.detail.column] = e.detail.value
				if(e.detail.column==1){
					this.showDeptDetail(e.detail.value)
					//this.multiIndex.splice(2, 1, 0)
				}
				if(e.detail.column==2){
					this.formData.deptId=this_.multiArrayId[2][e.detail.value]
				}
				this.$forceUpdate()
			},
			showDeptDetail(index){
				
				var this_=this
				this.urlRequestGet('system/dept/list?parentId='+this.company[index].deptId,'text/html;charset=UTF-8',function(res){
					if(res.statusCode='200'){
						let list=[]
						let listId=[]
						for (let i in res.data) {
							list.push(res.data[i].deptName)
							listId.push(res.data[i].deptId)
						}
						this_.multiArray[2]=list
						this_.multiArrayId[2]=listId
						this_.multiArray.splice(0,0)
						this_.multiArrayId.splice(0,0)
						this_.user.companyId=this_.multiArrayId[1][this_.multiIndex[1]]
						this_.user.deptId=this_.multiArrayId[2][this_.multiIndex[2]]
						//默认展示
						this_.multiIndex[1]=index
					}
				})
			}
			
		}
	}
</script>

<style>
	@import url("../../static/css/oa.css");
</style>
